___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
}

h1, h3 {
  margin: 0;
  font-weight: 600;
}

p {
  margin: 5px 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: underline;
}


#news-container {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

#news-container h1 {
  text-align: center;
  font-size: 2.5rem;
  color: #333;
}


#news-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.news-item {
  background-color: #fafafa;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.news-item h3 {
  font-size: 1.2rem;
  color: #0073e6;
}

.news-item p {
  font-size: 0.9rem;
  color: #555;
}

.news-item a {
  color: #0073e6;
}

.news-item a:hover {
  text-decoration: underline;
}


#load-more {
  background-color: #0073e6;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
  display: block;
  margin: 20px auto;
  transition: background-color 0.3s;
}

#load-more:hover {
  background-color: #005bb5;
}


#footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #222;
  color: #fff;
}

#footer p {
  font-size: 0.9rem;
  text-align: center;
  margin-top: 10px;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.social-links a {
  color: #fff;
  font-size: 1.5rem;
}

.social-links a:hover {
  color: #0073e6;
}

/* Media Queries for Responsiveness */
@media (max-width: 1024px) {
  #news-container {
    margin: 10px;
    padding: 15px;
  }

  #news-list {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  #news-container {
    padding: 10px;
  }

  #news-list {
    grid-template-columns: 1fr;
  }

  .social-links {
    flex-direction: column;
    align-items: center;
  }

  .social-links a {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  #news-container {
    padding: 10px;
  }

  .news-item {
    padding: 10px;
  }

  #load-more {
    font-size: 0.9rem;
  }

  #footer {
    padding: 15px;
  }
}
